<template>
  <div>
    <Tabla :users="tablaDatos"></Tabla>
    <vs-divider border-style="solid" color="#0a540a" ></vs-divider>
    <h1>Gráfica de selección por ruleta!</h1>
    <Grafica :seriesDatos="datos" :seriesXaxis="categories"></Grafica>
  </div>
</template>

<script>
import Tabla from '@/pages/Tabla.vue'
import Grafica from '@/pages/Grafica.vue'
export default {
  components: {
    Tabla,
    Grafica
  },
  props: ['usuarios'],
  computed: {
    tablaDatos: function () {
      let ordenarIndividuos = this.usuarios
      for (let index = 0; index < ordenarIndividuos.length; index++) {
        ordenarIndividuos[index].valorReal = parseFloat(
          ordenarIndividuos[index].valorReal
        )
        console.log(ordenarIndividuos[index].valorReal)
      }
      ordenarIndividuos.sort(function (a, b) {
        if (a.valorReal > b.valorReal) { return -1 } if (a.valorReal < b.valorReal) { return 1 } return 0
      })

      let vm = this
      for (let index = 0; index < ordenarIndividuos.length; index++) {
        vm.categories[index] = ordenarIndividuos[index].id
        vm.datos[index] = ordenarIndividuos[index].valorReal
      }

      return ordenarIndividuos
    }
  },
  data () {
    return {
      tablaOrdenada: [],
      categories: [],
      datos: []
    }
  }
}
</script>



