<template lang="html">
  <div>
    <vs-table stripe :data="users">
      <template slot="header">
        <h3>
          Tabla de Individuos
        </h3>
      </template>
      <template slot="thead">
        <vs-th>
          No. Individuo
        </vs-th>
        <vs-th>
          Individuo
        </vs-th>
        <vs-th>
          Valor Decimal
        </vs-th>
        <vs-th>
          Valor Real
        </vs-th>
      </template>

      <template slot-scope="{ data }">
        <vs-tr :state="indextr == 2 || indextr == 5?'success':indextr == 6?'danger':null" :key="indextr" v-for="(tr, indextr) in data">
          <vs-td :data="data[indextr].id">
            {{ data[indextr].id }}
          </vs-td>

          <vs-td :data="data[indextr].individuo">
            {{ data[indextr].individuo }}
          </vs-td>

          <vs-td :data="data[indextr].valorDecimal">
            {{ data[indextr].valorDecimal }}
          </vs-td>

          <vs-td :data="data[indextr].valorReal">
            {{ data[indextr].valorReal }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>
  </div>
</template>

<script>
export default {
  props: ['users']
}
</script>



