const user = {
  namespaced: true,
  state: {
    data: '',
    userLogged: false,
    token: '',
    profile: '',
    comedorLogged: undefined,
    sector: undefined
  },
  mutations: {
    allUsers (state, payload) {
      state.users = payload
    },
    userLogged  (state, payload) {
      state.data = payload
      state.userLogged = true
    },
    comedorLogged  (state, payload) {
      state.comedorLogged = payload
    },
    setToken (state, payload) {
      state.token = payload
    },
    setSector (state, payload) {
      state.sector = payload
    },
    setProfile (state, payload) {
      state.profile = payload
    },
    deleteUser (state, payload) {
      state.users = state.users.filter((user) => {
        return user._id !== payload
      })
    },
    logout (state, payload) {
      state.token = ''
      state.data = ''
      state.userLogged = false
      state.profile = ''
    }
  },
  actions: {
    userLogged ({commit}, payload) {
      commit('userLogged', payload)
    },
    comedorLogged ({commit}, payload) {
      commit('comedorLogged', payload)
    },
    setSector ({commit}, payload) {
      return new Promise((resolve, reject) => {
        commit('setSector', payload)
        resolve()
      })
    },
    setToken ({commit}, payload) {
      commit('setToken', payload)
    },
    setProfile ({commit}, payload) {
      commit('setProfile', payload)
    },
    allUsers ({commit}, payload) {
      commit('allUsers', payload)
    },
    deleteUser ({commit}, payload) {
      commit('deleteUser', payload)
    },
    logout ({commit}) {
      commit('logout')
    }
  },
  getters: {}
}

export default user



