const objetos = {
  state: {
    objetosAsegurados: {
      vehiculos: [],
      otros: [],
      armas_blancas: [],
      drogas: []
    }
  },
  mutations: {
    setOtros (state, payload) {
      state.objetosAsegurados.otros.push(payload)
    },
    setVehiculos (state, payload) {
      payload.marca = payload.marca.id
      payload.submarca = payload.submarca.id
      state.objetosAsegurados.vehiculos.push(payload)
    },
    setArmasBlancas (state, payload) {
      state.objetosAsegurados.armas_blancas.push(payload)
    },
    setDrogas (state, payload) {
      state.objetosAsegurados.drogas.push(payload)
    },
    clearObjects (state) {
      state.objetosAsegurados = {
        vehiculos: [],
        otros: [],
        armas_blancas: [],
        drogas: []
      }
    }
  },
  actions: {
    setOtros ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        commit('setOtros', payload)
        resolve()
      })
    },
    setVehiculos ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        commit('setVehiculos', payload)
        resolve()
      })
    },
    setArmasBlancas ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        commit('setArmasBlancas', payload)
        resolve()
      })
    },
    setDrogas ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        commit('setDrogas', payload)
        resolve()
      })
    },
    clearObjects ({ commit }) {
      commit('clearObjects')
    }
  },
  getters: {}
}

export default objetos



