<template>
  <div>
    <h1>Cruce de dos puntos!</h1>
    <br />
    <h2>Tabla original</h2>
    <Tabla :users="tablaDatosO"></Tabla>
    <br />
    <vs-divider border-style="solid" color="#0a540a" ></vs-divider>
    <h2>Gráfica de original!</h2>
    <Grafica :seriesDatos="datos" :seriesXaxis="categories"></Grafica>
    <br />
    <vs-divider border-style="solid" color="#0a540a" ></vs-divider>
    <h2>Jerarquia por ruleta!</h2>
    <SeleccionRuleta :usuarios="tablaDatosO"></SeleccionRuleta>
    <h2>¿Realizar cruce de 2 puntos?</h2>
    <vs-input
      type="number"
      label="Introduce el valor de inicio"
      placeholder="valor inicio"
      v-model="inicio"
    />
    <vs-input
      type="number"
      label="Introduce el valor de fin"
      placeholder="valor fin"
      v-model="fin"
    />
    <br />
    <vs-button @click="cruce_un_individuo(inicio, fin)"
      >Realizar cruce</vs-button
    >
    <br />
    <div style="float: right;">
      <vs-chip color="primary" style="font-size: 17px;">
        Valor minimo: {{ valorMinimoCruce }}
      </vs-chip>
      <vs-chip color="danger" style="font-size: 17px;">
        Valor máximo: {{ valorMaximoCruce }}
      </vs-chip>
    </div>
    <Tabla :users="tablaDatosCruce"></Tabla>
    <h2>¿Realizar gráfica de cruce?</h2>
    <vs-button @click="dibujar_grafica_cruce(tablaDatosCruce)"
      >Realizar gráfica</vs-button
    >
    <div id="chart" v-if="activar_grafica_cruce">
      <Grafica :seriesDatos="datosC" :seriesXaxis="categoriesC"></Grafica>
    </div>
  </div>
</template>
<script>
import Tabla from '@/pages/Tabla.vue'
import SeleccionRuleta from '@/pages/SeleccionRuleta.vue'
import Grafica from '@/pages/Grafica.vue'
export default {
  components: {
    Tabla,
    SeleccionRuleta,
    Grafica
  },
  props: ['tabla'],
  computed: {
    tablaDatosO: function () {
      let tablaDatos = this.tabla.slice()
      let vm = this
      //   let ve = 0;
      //   for (let i = 0; i < tablaDatos.length; i++) {
      //     ve = vm.min + (vm.max - vm.min) * (i + 1) - 1 / (tablaDatos.length - 1);
      //     tablaDatos[i].valorReal = ve;
      //   }
      for (let index = 0; index < tablaDatos.length; index++) {
        vm.categories[index] = tablaDatos[index].id
        vm.datos[index] = tablaDatos[index].valorReal
      }

      return tablaDatos
    }
  },
  methods: {
    dibujar_grafica_cruce (tablaDatosCruce) {
      let vm = this
      for (let index = 0; index < tablaDatosCruce.length; index++) {
        vm.categoriesC[index] = tablaDatosCruce[index].id
        vm.datosC[index] = parseFloat(tablaDatosCruce[index].valorReal)
      }
      vm.activar_grafica_cruce = !vm.activar_grafica_cruce
    },
    actualizar_tabla_cruce () {
      let vm = this
      let vmaximoC = vm.tablaDatosCruce[0].valorReal
      parseFloat(vmaximoC)
      for (let index = 0; index < vm.tablaDatosCruce.length; index++) {
        if (vmaximoC > parseFloat(vm.tablaDatosCruce[index].valorReal)) {
          // vmaximoC = vmaximoC;
        } else {
          vmaximoC = parseFloat(vm.tablaDatosCruce[index].valorReal)
        }
      }

      vm.valorMaximoCruce = vmaximoC

      let vminimoC = vm.tablaDatosCruce[0].valorReal
      parseFloat(vminimoC)
      for (let index = 0; index < vm.tablaDatosCruce.length; index++) {
        if (vminimoC > parseFloat(vm.tablaDatosCruce[index].valorReal)) {
          vminimoC = parseFloat(vm.tablaDatosCruce[index].valorReal)
        }
      }

      if (vm.tablaDatosCruce.length > 0) {
        vm.valorMinimoC = '0.0'
      }

      vm.valorMinimoCruce = vminimoC
    },
    funcion_adaptacion_funcion (numeroDecimal) {
      // Funcion de adaptacion f(x) = 5 * cos(x) + 2(x) -> x es el valor decimal
      let funcionApatacion = 5 * Math.cos(numeroDecimal) + 2 * numeroDecimal
      return funcionApatacion.toFixed(3)
    },
    binario_decimal_funcion (elemento) {
      let numeroBinario = elemento
      let decimal = 0
      let index = 0
      for (let exp = elemento.length - 1; exp >= 0; exp--) {
        let numero = parseInt(numeroBinario[index])
        numero = numero * Math.pow(2, exp)
        decimal = decimal + numero
        index += 1
      }
      return decimal
    },
    indAle () {
      let vm = this
      // let b = Math.floor(Math.random() * 10);
      // let a = b < vm.tablaDatos.length ? b : vm.indAle();
      let a = Math.floor(Math.random() * vm.tablaDatosO.length)
      return a
    },
    cruce_un_individuo (inicio, fin) {
      let vm = this
      vm.activar_grafica_cruce = false
      let veinte = 0.25 * vm.tablaDatosO.length
      let setenta = 0.75 * vm.tablaDatosO.length
      console.log('iteraciones: ' + Math.round(setenta))
      console.log('suma: ' + (Math.round(veinte) + Math.round(setenta)))
      for (let iteraciones = 0; iteraciones < setenta; iteraciones++) {
        let arreglo = []
        let ind = 0
        let valAnt = 0
        let comparacionValor = () => {
          // console.log('valors: p1 " + (valAnt + 1) + " - p2 " + (ind + 1));
          if (ind === valAnt) {
            ind = vm.indAle()
            comparacionValor()
          }
        }

        for (let i = 0; i < 2; i++) {
          ind = vm.indAle()
          if (i === 1) {
            comparacionValor()
            arreglo.push(vm.tablaDatosO[ind])
          } else {
            valAnt = ind
            arreglo.push(vm.tablaDatosO[ind])
          }
        }

        let a = arreglo[0].individuo + ' '
        let b = arreglo[1].individuo + ' '
        // let decA = vm.funcion_adaptacion_funcion(vm.binario_decimal_funcion(a))
        // let decB = vm.funcion_adaptacion_funcion(vm.binario_decimal_funcion(b))
        let valorM = arreglo[0]

        for (let index = 0; index < arreglo.length; index++) {
          if (valorM < arreglo[index]) {
            valorM = arreglo[index]
          }
        }
        let primerMitadP1 = ''
        let segundaMitadP1 = ''
        let primerMitadP2 = ''
        let segundaMitadP2 = ''
        let terceraMitadP1 = ''
        let terceraMitadP2 = ''

        // CAMBIAR URGENTE!!!!! Math.round!
        // NUMEROS PARES
        //   let indiceOtraMitad = a.length / 2;
        //   for (let index = 0; index < a.length / 2; index++) {
        //     primerMitadP1 = primerMitadP1 + a[index];
        //     segundaMitadP1 = segundaMitadP1 + a[indiceOtraMitad];
        //     primerMitadP2 = primerMitadP2 + b[index];
        //     segundaMitadP2 = segundaMitadP2 + b[indiceOtraMitad];
        //     indiceOtraMitad += 1;
        //   }
        console.log(inicio)
        primerMitadP1 = a.slice(-a.length, fin)
        segundaMitadP1 = a.slice(-(fin + 1), -inicio)
        terceraMitadP1 = a.slice(-inicio, -1)

        primerMitadP2 = b.slice(-a.length, fin)
        segundaMitadP2 = b.slice(-(fin + 1), -inicio)
        terceraMitadP2 = b.slice(-inicio, -1)

        let hijo1 = primerMitadP1 + segundaMitadP2 + terceraMitadP1
        let hijo2 = primerMitadP2 + segundaMitadP1 + terceraMitadP2

        let decHijo1 = vm.binario_decimal_funcion(hijo1)
        let realHijo1 = vm.funcion_adaptacion_funcion(decHijo1)
        let decHijo2 = vm.binario_decimal_funcion(hijo2)
        let realHijo2 = vm.funcion_adaptacion_funcion(decHijo2)

        let perdedorP =
          parseFloat(arreglo[0].valorReal) < parseFloat(arreglo[1].valorReal)
            ? arreglo[0]
            : arreglo[1]

        let ganadorH =
          realHijo1 < realHijo2
            ? {individuo: hijo2, valorDecimal: decHijo2, valorReal: realHijo2, id: perdedorP.id}
            : {individuo: hijo1, valorDecimal: decHijo1, valorReal: realHijo1, id: perdedorP.id}
        console.log(`-----------iteracion-${iteraciones}-----------------`)
        console.log('id: ' + arreglo[0].id + ' padre 1: ' + a + ' vreal: ' + arreglo[0].valorReal)
        console.log('id: ' + arreglo[1].id + ' padre 2: ' + b + ' vreal: ' + arreglo[1].valorReal)
        console.log('id: ' + perdedorP.id + ' padre perdedor: ' + perdedorP.individuo)

        console.log('hijo 1: ' + hijo1 + ' vreal: ' + realHijo1)
        console.log('hijo 2: ' + hijo2 + ' vreal: ' + realHijo2)
        console.log('hijo ganador: ' + ganadorH.individuo)
        console.log('///////////////////////////////////////')
        vm.tablaDatosCruce = vm.tablaDatosO.slice()
        vm.tablaDatosCruce.splice(perdedorP.id - 1, 1, ganadorH)
        vm.actualizar_tabla_cruce()
      }
    }
  },

  data () {
    return {
      categories: [],
      datos: [],
      categoriesC: [],
      datosC: [],
      tablaDatos: [],
      tablaDatosCruce: [],
      activar_grafica: true,
      activar_grafica_cruce: false,
      boolCruceUnInd: false,
      contador: 0,
      valorMinimo: '0.0',
      valorMinimoCruce: '0.0',
      valorMaximo: '0.0',
      valorMaximoCruce: '0.0',
      id_elemento: '',
      numeroBinario: '',
      numeroDecimal: '',
      numeroReal: '',
      inicio: '',
      fin: ''
    }
  }
}
</script>

<style></style>



