<template>
  <apexchart
    type="area"
    height="350"
    :options="chartOptions"
    :series="series"
  ></apexchart>
</template>

<script>
export default {
  props: ['seriesDatos', 'seriesXaxis', 'seriesDatosCruce'],
  data () {
    return {
      series: [
        {
          name: 'Valor real',
          data: this.seriesDatos
        },
        {
          name: 'Valor real cruce',
          data: this.seriesDatosCruce
        }
      ],
      chartOptions: {
        title: {
          text: 'Ambas gráficas',
          align: 'left'
        },
        chart: {
          height: 350,
          type: 'area'
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: 'smooth'
        },
        xaxis: {
          categories: this.seriesXaxis
        },
        tooltip: {
          x: {
            format: 'dd/MM/yy HH:mm'
          }
        }
      }
    }
  }
}
</script>

<style></style>



