// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import App from '@/App'
import router from '@/router'
import VueAuthenticate from 'vue-authenticate'
import 'material-icons/iconfont/material-icons.css'
import vSelect from 'vue-select'

// NON Vuesax Components
import { Upload } from 'element-ui'
import VueProgressBar from 'vue-progressbar'

// VueAxios
import VueAxios from 'vue-axios'

// Vuex Store
import store from '@/store'

// Vuesax
import Vuesax from 'vuesax'
import 'vuesax/dist/vuesax.css'

// Prism
import 'prismjs'
import 'prismjs/themes/prism.css'

// Styles: SCSS
import '@/assets/scss/main.scss'

// Tailwind
import '@/assets/css/main.css'

import '@/globalComponents'
import '@/themeConfig'

// Vuejs - Vue wrapper for hammerjs
import { VueHammer } from 'vue2-hammer'

// FeatherIcons
import FeatherIcon from '@/components/FeatherIcon.vue'

import { ValidationProvider, ValidationObserver, extend, configure } from 'vee-validate'
import * as rules from 'vee-validate/dist/rules'
import { messages } from 'vee-validate/dist/locale/es.json'

import flatPickr from 'flatpickr'

// vWS
import VueNativeSock from 'vue-native-websocket'

// Vue Idle
import IdleVue from 'idle-vue'

// VueApexCharts
import VueApexCharts from 'vue-apexcharts'

Vue.component('v-select', vSelect)
Vue.component('apexchart', VueApexCharts)
Object.keys(rules).forEach(rule => {
  extend(rule, {
    ...rules[rule],
    message: messages[rule]
  })
})

configure({
  classes: {
    valid: 'is-valid',
    invalid: 'is-invalid',
    dirty: ['is-invalid-feedback']
  }
})

Vue.component('ValidationProvider', ValidationProvider)
Vue.component('ValidationObserver', ValidationObserver)

const Spanish = require('flatpickr/dist/l10n/es.js').default.es
// or import { Russian } from "flatpickr/dist/l10n/ru.js"
flatPickr.localize(Spanish) // default locale is now Russian

// Feather icons
Vue.component(FeatherIcon.name, FeatherIcon)

// Feather font icon
require('@/assets/css/iconfont.css')

// Mi Policia icons
require('@/assets/fonts/style.css')

Vue.use(VueHammer)
Vue.use(Vuesax)
Vue.use(VueAxios, axios)
const moment = require('moment')
require('moment/locale/es')

Vue.use(require('vue-moment'), {
  moment
})

Vue.use(Upload)

Vue.use(VueProgressBar, {
  color: 'rgb(59, 116, 255)',
  failedColor: 'red',
  thickness: '8px'
})

console.log(Vue.moment().locale()) // es

// Vue Auth
Vue.use(VueAuthenticate, {
  baseUrl: 'http://localhost:8080/',
  bindRequestInterceptor () {
    this.$http.interceptors.request.use((config) => {
      if (this.isAuthenticated()) {
        config.headers['Authorization'] = [
          this.options.tokenType, this.getToken()
        ].join(' ')
      } else {
        delete config.headers['Authorization']
      }
      return config
    })
  },
  bindResponseInterceptor () {
    this.$http.interceptors.response.use((response) => {
      console.log(response)
      this.setToken(response)
      return response
    })
  }
})

Vue.use(VueNativeSock, 'ws://192.168.0.16:8020/ws', {
  connectManually: true
})

Vue.use(IdleVue, {
  idleTime: 1000000000,
  startAtIdle: false,
  store
})

// Vue.directive('uppercase',
//   {
//     inserted: function (el, _, vnode) {
//       el.addEventListener('input', async function (e) {
//         e.target.value = e.target.value.toUpperCase()
//         vnode.componentInstance.$emit('input', e.target.value.toUpperCase())
//       })
//     }
//   })

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



