<template lang="html">
  <div>
    <Header></Header>
    <vs-row vs-justify="center">
      <vs-col vs-w="11">
        <vs-card>
          <div>
            <vs-tabs alignment="center" color="#0a540a">
              <vs-tab label="INICIO" class="scroll">
                <vs-alert
                  title="IMPORTANTE!"
                  active="true"
                  color="success"
                >
                  El siguiente programa solo admite individuos de tipo pares, si
                  se meten individuos impares no sufrira ningun cambio.
                </vs-alert>
                <br />
                <vs-row>
                  <vs-col vs-lg="3" vs-xs="6" vs-justify="space-around">
                    <div class="text-left">
                      <h1>INTRODUCE LO REQUERIDO</h1>
                      <ValidationObserver v-slot="{ invalid }">
                        <form @submit.prevent="handleSubmit(agregar_elemento)">
                          <ValidationProvider
                            name="Longitud"
                            :rules="{ required: true, min: 1, max: 5 }"
                            v-slot="{ errors }"
                          >
                            <br />
                            <h5 class="titulos">Longitud del individuo</h5>
                            <vs-input
                              :success="!invalid"
                              type="number"
                              label="Introduce la longitud del cromosoma"
                              placeholder="Longitud"
                              v-model="vmin"
                            />
                            <span>{{ errors[0] }}</span>
                          </ValidationProvider>

                          <ValidationProvider
                            name="INDIVIDUO"
                            :rules="{ required: true, min: vmin, max: vmin }"
                            v-slot="{ errors }"
                          >
                            <br />
                            <h5 class="titulos">Agregar Individuo</h5>
                            <vs-input
                              :success="!invalid"
                              type="number"
                              label="Introduce tu cadena de bits"
                              placeholder="Cadena de 10 bits"
                              v-model="numeroBinario"
                            />
                            <span>{{ errors[0] }}</span>
                          </ValidationProvider>

                          <br />
                          <vs-button
                            color="success"
                            type="filled"
                            :disabled="invalid"
                            @click="agregar_elemento"
                            >Agregar individuo</vs-button
                          >
                        </form>
                      </ValidationObserver>
                      <br />
                      <ValidationObserver v-slot="{ invalid }">
                        <form @submit.prevent="handleSubmit(agregar_elemento)">
                          <ValidationProvider
                            name="No. individuo"
                            :rules="{ required: true, min: 1, max: 5 }"
                            v-slot="{ errors }"
                          >
                            <h5 class="titulos">Eliminar Individuo</h5>
                            <vs-input
                              :success="!invalid"
                              type="number"
                              label="Introduce el No. de inviduo"
                              placeholder="No. individuo"
                              v-model="id_elemento"
                            />
                            <span>{{ errors[0] }}</span>
                          </ValidationProvider>
                          <br />
                          <vs-button
                            color="danger"
                            type="filled"
                            :disabled="invalid"
                            @click="eliminar_elemento"
                            >Eliminar individuo</vs-button
                          >
                        </form>
                      </ValidationObserver>
                      <br />
                    </div>
                  </vs-col>
                  <vs-col vs-lg="9" vs-xs="6">
                    <div style="float: right;">
                      <vs-chip color="primary" style="font-size: 17px;">
                        Valor minimo: {{ valorMinimo }}
                      </vs-chip>
                      <vs-chip color="danger" style="font-size: 17px;">
                        Valor máximo: {{ valorMaximo }}
                      </vs-chip>
                    </div>
                    <Tabla :users="tablaDatos"></Tabla>
                    <br />
                    <div
                      class="text-center"
                      v-if="tablaDatos.length > 3 ? true : false"
                    >
                      <h1>¿Realizar gráfica?</h1>
                      <br />
                      <vs-button @click="dibujar_grafica(tablaDatos)"
                        >Realizar gráfica</vs-button
                      >
                      <div id="chart" v-if="activar_grafica">
                        <Grafica
                          :seriesDatos="datos"
                          :seriesXaxis="categories"
                        ></Grafica>
                      </div>
                      <h1>¿Realizar cruce de un individuo?</h1>
                      <vs-button @click="cruce_un_individuo"
                        >Realizar cruce</vs-button
                      >
                      <br />
                      <div style="float: right;">
                        <vs-chip color="primary" style="font-size: 17px;">
                          Valor minimo: {{ valorMinimoCruce }}
                        </vs-chip>
                        <vs-chip color="danger" style="font-size: 17px;">
                          Valor máximo: {{ valorMaximoCruce }}
                        </vs-chip>
                      </div>
                      <Tabla :users="tablaDatosCruce"></Tabla>
                      <h1>¿Realizar gráfica de cruce?</h1>
                      <vs-button @click="dibujar_grafica_cruce(tablaDatosCruce)"
                        >Realizar gráfica</vs-button
                      >
                      <div id="chart" v-if="activar_grafica_cruce">
                        <Grafica
                          :seriesDatos="datosC"
                          :seriesXaxis="categoriesC"
                        ></Grafica>
                        <AmbasGraficas
                          :seriesDatos="datos"
                          :seriesXaxis="categories"
                          :seriesDatosCruce="datosC"
                        ></AmbasGraficas>
                        <h1>
                          IR AL BOTON SELECCION POR RULETA PARA VER LOS CAMBIOS
                        </h1>
                      </div>
                    </div>
                  </vs-col>
                </vs-row>
              </vs-tab>
              <vs-tab label="SELECCION POR RULETA">
                <div>
                  <vs-alert
                    title="ALERTA!"
                    active="true"
                    color="warning"
                  >
                    Esta seccion se basa con los calculos anteriores.
                  </vs-alert>
                  <br />
                  <SeleccionRuleta
                    :usuarios="tablaDatosCruce"
                  ></SeleccionRuleta>
                </div>
              </vs-tab>
              <vs-tab label="SELECCIÓN POR JERARQUÍA">
                <div>
                  <vs-alert
                    title="IMPORTANTE!"
                    active="true"
                  >
                    Esta seccion se basa en los individuos originales!.
                  </vs-alert>
                  <br />
                  <SeleccionJerarquia
                    :tabla="tablaDatos"
                    :min="valorMinimo"
                    :max="valorMaximo"
                  ></SeleccionJerarquia>
                </div>
              </vs-tab>
              <vs-tab label="CRUCE DE DOS PUNTOS">
                <div>
                   <vs-alert
                    title="IMPORTANTE!"
                    active="true"
                  >
                    Esta seccion se basa en los individuos originales!.
                  </vs-alert>
                  <br />
                  <CruceDosPuntos :tabla="tablaDatos"></CruceDosPuntos>
                </div>
              </vs-tab>
            </vs-tabs>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
import Logo from '../layouts/components/Logo.vue'
import Tabla from '@/pages/Tabla.vue'
import Header from '@/pages/Header.vue'
import Grafica from '@/pages/Grafica.vue'
import AmbasGraficas from '@/pages/AmbasGraficas.vue'
import SeleccionRuleta from '@/pages/SeleccionRuleta.vue'
import SeleccionJerarquia from '@/pages/SeleccionJerarquia.vue'
import CruceDosPuntos from '@/pages/CruceDosPuntos.vue'
export default {
  mounted () {
    this.binario_decimal()
  },
  methods: {
    valores_aleatorio (tamanoLista, longitudInd) {
      let listaRetunr = []
      for (let j = 0; j < tamanoLista; j++) {
        let A = ''
        for (let i = 0; i < longitudInd; i++) {
          A = A + listaRetunr[Math.floor(Math.random() * listaRetunr.length)]
        }
        listaRetunr.push(A)
        console.log(A)
        A = ''
      }
      return listaRetunr
    },
    dibujar_grafica_cruce (tablaDatosCruce) {
      let vm = this
      for (let index = 0; index < tablaDatosCruce.length; index++) {
        vm.categoriesC[index] = tablaDatosCruce[index].id
        vm.datosC[index] = parseFloat(tablaDatosCruce[index].valorReal)
      }
      vm.activar_grafica_cruce = true
    },
    actualizar_tabla_cruce () {
      let vm = this

      let vmaximoC = vm.tablaDatosCruce[0].valorReal
      parseFloat(vmaximoC)
      for (let index = 0; index < vm.tablaDatosCruce.length; index++) {
        if (vmaximoC > parseFloat(vm.tablaDatosCruce[index].valorReal)) {
          // vmaximoC = vmaximoC;
        } else {
          vmaximoC = parseFloat(vm.tablaDatosCruce[index].valorReal)
        }
      }

      vm.valorMaximoCruce = vmaximoC

      let vminimoC = vm.tablaDatosCruce[0].valorReal
      parseFloat(vminimoC)
      for (let index = 0; index < vm.tablaDatosCruce.length; index++) {
        if (vminimoC > parseFloat(vm.tablaDatosCruce[index].valorReal)) {
          vminimoC = parseFloat(vm.tablaDatosCruce[index].valorReal)
        }
      }

      if (vm.tablaDatosCruce.length > 0) {
        vm.valorMinimoC = '0.0'
      }

      vm.valorMinimoCruce = vminimoC
    },
    actualizar_tabla () {
      let vm = this
      for (let index = 0; index < vm.tablaDatos.length; index++) {
        vm.tablaDatos[index].id = index + 1
      }
      vm.valor_minimo()
      vm.valor_maximo()
      if (vm.tablaDatos.length === 0) {
        vm.valorMaximo = '0.0'
        vm.valorMinimoo = '0.0'
      }
    },
    binario_decimal () {
      let vm = this
      let numeroBinario = vm.numeroBinario
      let decimal = 0
      let index = 0
      for (let exp = vm.numeroBinario.length - 1; exp >= 0; exp--) {
        let numero = parseInt(numeroBinario[index])
        numero = numero * Math.pow(2, exp)
        decimal = decimal + numero
        index += 1
      }
      vm.numeroDecimal = decimal
    },
    dibujar_grafica (tablaDatos) {
      let vm = this
      for (let index = 0; index < tablaDatos.length; index++) {
        vm.categories[index] = tablaDatos[index].id
        vm.datos[index] = parseFloat(tablaDatos[index].valorReal)
      }
      vm.activar_grafica = true
    },
    funcion_adaptacion () {
      // Funcion de adaptacion f(x) = 5 * cos(x) + 2(x) -> x es el valor decimal
      let vm = this
      let funcionApatacion =
        5 * Math.cos(vm.numeroDecimal) + 2 * vm.numeroDecimal
      vm.numeroReal = funcionApatacion.toFixed(4)
    },
    agregar_elemento () {
      let vm = this
      vm.activar_grafica = false
      vm.activar_grafica_cruce = false
      vm.binario_decimal()
      vm.funcion_adaptacion()
      if (vm.numeroDecimal !== 0) {
        vm.contador = vm.contador + 1
        vm.tablaDatos.push({
          individuo: vm.numeroBinario,
          valorDecimal: vm.numeroDecimal,
          valorReal: vm.numeroReal
        })

        for (let index = 0; index < vm.tablaDatos.length; index++) {
          vm.tablaDatos[index].id = index + 1
        }
        vm.numeroBinario = ''
      } else {
        this.$vs.notify({
          title: 'ERROR!',
          text: `No se admiten individuos en ceros`,
          color: 'danger'
        })
      }
      vm.valor_minimo()
      vm.valor_maximo()
    },
    eliminar_elemento () {
      try {
        let vm = this
        if (
          vm.id_elemento - 1 < vm.tablaDatos.length &&
          !(vm.id_elemento === 0) &&
          vm.id_elemento > 0
        ) {
          this.$vs.notify({
            title: 'Individuo eliminado',
            text: `Eliminaste el individuo que estaba en ${vm.id_elemento}`,
            color: 'warning'
          })
        } else {
          this.$vs.notify({
            title: 'INDICE INEXISTENTE',
            text: `sin elemento`,
            color: 'danger'
          })
        }
        if (!(vm.id_elemento - 1 < 0)) {
          vm.tablaDatos.splice(vm.id_elemento - 1, 1)
          vm.id_elemento = ''
        }
        for (let index = 0; index < vm.tablaDatos.length; index++) {
          vm.tablaDatos[index].id = index + 1
        }
        if (vm.tablaDatos.length === 0) {
          vm.valorMaximo = '0.0'
          vm.valorMinimo = '0.0'
        }
        vm.valor_maximo()
        vm.valor_minimo()
      } catch (error) {
        console.log('no hay elementos')
        this.$vs.notify({
          title: 'SIN ELEMENTOS',
          text: 'Estas tratando de eliminar un individuos que no existen',
          color: 'danger'
        })
      }
    },
    valor_minimo () {
      let vm = this
      let vminimo = vm.tablaDatos[0].valorReal
      parseFloat(vminimo)
      for (let index = 0; index < vm.tablaDatos.length; index++) {
        if (vminimo > parseFloat(vm.tablaDatos[index].valorReal)) {
          vminimo = parseFloat(vm.tablaDatos[index].valorReal)
        }
      }
      if (vm.tablaDatos.length > 0) {
        vm.valorMinimo = '0.0'
      }
      vm.valorMinimo = vminimo
    },
    valor_maximo () {
      let vm = this
      let vmaximo = vm.tablaDatos[0].valorReal
      parseFloat(vmaximo)
      for (let index = 0; index < vm.tablaDatos.length; index++) {
        if (vmaximo > parseFloat(vm.tablaDatos[index].valorReal)) {
          // vmaximo = vmaximo
        } else {
          vmaximo = parseFloat(vm.tablaDatos[index].valorReal)
        }
      }

      vm.valorMaximo = vmaximo
    },
    binario_decimal_funcion (elemento) {
      let numeroBinario = elemento
      let decimal = 0
      let index = 0
      for (let exp = elemento.length - 1; exp >= 0; exp--) {
        let numero = parseInt(numeroBinario[index])
        numero = numero * Math.pow(2, exp)
        decimal = decimal + numero
        index += 1
      }
      return decimal
    },
    funcion_adaptacion_funcion (numeroDecimal) {
      // Funcion de adaptacion f(x) = 5 * cos(x) + 2(x) -> x es el valor decimal
      let funcionApatacion = 5 * Math.cos(numeroDecimal) + 2 * numeroDecimal
      return funcionApatacion.toFixed(3)
    },
    valor_maximo_funcion (nuevaTabla) {
      // let vm = this
      let vmaximo = nuevaTabla[0].valorReal
      parseFloat(vmaximo)
      for (let index = 0; index < nuevaTabla.length; index++) {
        if (vmaximo > parseFloat(nuevaTabla[index].valorReal)) {
          // vmaximo = vmaximo
        } else {
          vmaximo = parseFloat(nuevaTabla[index].valorReal)
        }
      }
      return vmaximo
    },
    indAle () {
      let vm = this
      // let b = Math.floor(Math.random() * 10);
      // let a = b < vm.tablaDatos.length ? b : vm.indAle();
      let a = Math.floor(Math.random() * vm.tablaDatos.length)
      return a
    },
    cruce_un_individuo () {
      let vm = this
      // console.log(typeof(vm.tablaDatos.length))
      vm.activar_grafica_cruce = false
      let setenta = 0.75 * vm.tablaDatos.length
      console.log('iteraciones: ' + Math.round(setenta))

      for (let iteraciones = 0; iteraciones < setenta; iteraciones++) {
        let arreglo = []
        let ind = 0
        let valAnt = 0
        let comparacionValor = () => {
          // console.log("valors: p1 " + (valAnt + 1) + " - p2 " + (ind + 1));
          if (ind === valAnt) {
            ind = vm.indAle()
            comparacionValor()
          }
        }

        for (let i = 0; i < 2; i++) {
          ind = vm.indAle()
          if (i === 1) {
            comparacionValor()
            arreglo.push(vm.tablaDatos[ind])
          } else {
            valAnt = ind
            arreglo.push(vm.tablaDatos[ind])
          }
        }

        let a = arreglo[0].individuo
        let b = arreglo[1].individuo
        // let decA = vm.funcion_adaptacion_funcion(vm.binario_decimal_funcion(a));
        // let decB = vm.funcion_adaptacion_funcion(vm.binario_decimal_funcion(b));
        let valorM = arreglo[0]

        for (let index = 0; index < arreglo.length; index++) {
          if (valorM < arreglo[index]) {
            valorM = arreglo[index]
          }
        }
        let primerMitadP1 = ''
        let segundaMitadP1 = ''
        let primerMitadP2 = ''
        let segundaMitadP2 = ''

        // CAMBIAR URGENTE!!!!! Math.round!
        if (a.length % 2 === 0) {
          // NUMEROS PARES
          let indiceOtraMitad = a.length / 2
          for (let index = 0; index < a.length / 2; index++) {
            primerMitadP1 = primerMitadP1 + a[index]
            segundaMitadP1 = segundaMitadP1 + a[indiceOtraMitad]
            primerMitadP2 = primerMitadP2 + b[index]
            segundaMitadP2 = segundaMitadP2 + b[indiceOtraMitad]
            indiceOtraMitad += 1
          }
          let hijo1 = primerMitadP1 + segundaMitadP2
          let hijo2 = primerMitadP2 + segundaMitadP1

          let decHijo1 = vm.binario_decimal_funcion(hijo1)
          let realHijo1 = vm.funcion_adaptacion_funcion(decHijo1)
          let decHijo2 = vm.binario_decimal_funcion(hijo2)
          let realHijo2 = vm.funcion_adaptacion_funcion(decHijo2)

          let perdedorP =
            parseFloat(arreglo[0].valorReal) < parseFloat(arreglo[1].valorReal)
              ? arreglo[0]
              : arreglo[1]

          let ganadorH =
            realHijo1 < realHijo2
              ? {individuo: hijo2, valorDecimal: decHijo2, valorReal: realHijo2, id: perdedorP.id}
              : {individuo: hijo1, valorDecimal: decHijo1, valorReal: realHijo1, id: perdedorP.id}
          console.log(`-----------iteracion-${iteraciones}-----------------`)
          console.log('id: ' + arreglo[0].id + ' padre 1: ' + a + ' vreal: ' + arreglo[0].valorReal)
          console.log('id: ' + arreglo[1].id + ' padre 2: ' + b + ' vreal: ' + arreglo[1].valorReal)
          console.log('id: ' + perdedorP.id + ' padre perdedor: ' + perdedorP.individuo)

          console.log('hijo 1: ' + hijo1 + ' vreal: ' + realHijo1)
          console.log('hijo 2: ' + hijo2 + ' vreal: ' + realHijo2)
          console.log('hijo ganador: ' + ganadorH.individuo)
          console.log('///////////////////////////////////////')
          vm.tablaDatosCruce = vm.tablaDatos.slice()
          vm.tablaDatosCruce.splice(perdedorP.id - 1, 1, ganadorH)
          vm.actualizar_tabla_cruce()
        } else {
          // NUMEROS IMPARES
          let indiceOtraMitad = Math.floor(a.length / 2)
          for (let index = 0; index < Math.floor(a.length / 2); index++) {
            primerMitadP1 = primerMitadP1 + a[index]
            primerMitadP2 = primerMitadP2 + a[index]
          }
          for (let index = indiceOtraMitad; index < a.length; index++) {
            segundaMitadP1 = segundaMitadP1 + a[index]
            segundaMitadP2 = segundaMitadP2 + a[index]
          }
        }
      }
    }
  },
  components: {
    Logo,
    Tabla,
    Header,
    Grafica,
    AmbasGraficas,
    SeleccionRuleta,
    SeleccionJerarquia,
    CruceDosPuntos
  },
  data () {
    return {
      categories: [],
      datos: [],
      categoriesC: [],
      datosC: [],
      tablaDatos: [],
      tablaDatosCruce: [],
      activar_grafica: true,
      activar_grafica_cruce: true,
      boolCruceUnInd: false,
      contador: 0,
      valorMinimo: '0.0',
      valorMinimoCruce: '0.0',
      valorMaximo: '0.0',
      valorMaximoCruce: '0.0',
      id_elemento: '',
      vmin: '',
      numeroBinario: '',
      numeroDecimal: '',
      numeroReal: ''
    }
  }
}
</script>
<style>
.scroll {
  overflow-x: auto;
}
.titulos {
  color: #0a540a;
}
</style>



